import React from 'react'
import Mytap from './Mytap'

const Egoods = (props) => {
    return (
        <div>
            <img src={require('../images/16.jpg')} style={{width:'100%'}}/>
            <button style={{
                width:'35px',
                height: '35px',
                borderRadius:'10rem',
                border:0,
                position: 'relative',
                top:'-360px',
                left:'10px',
                display:'block',
                background:'rgba(0, 0, 0, 0.4)',
                color: '#fff'}}
                onClick={props.history.goBack}
            > <i className='iconfont icon-shangyiji'  ></i> </button>
             <Mytap />
        </div>
    )
}

export default Egoods
